.App {
    display: grid;
    width: 1200px;
    height: 100vh;
    margin: 0 auto;
    overflow: hidden;
    grid-template-areas:
    'h h'
    'n c';
    grid-auto-rows: 60px 1fr;
    grid-auto-columns: 2fr 10fr;
}

.appWrapperContent {
    grid-area: c;
    background-color: white;
}


